import _ from 'lodash'
import { Message } from 'element-ui';
import Vue from 'vue';
import store from '@/store';
import router from '@/router';


export default async function pullData(constraint = false) {
    
    const timeDifference = new Date() - store.state.userData.user.info.updated;
    if (timeDifference > 10000 || !timeDifference || constraint){
        let msg;
        if (!store.state.userData.notice.init){
            msg = Vue.prototype.$toastx({type:'info',title: '拉取数据中，请稍等'},{
                timeout: false
            });
            // msg = Message.warning({
            //     message: '拉取数据中，请稍等',
            //     showClose: true,
            //     duration: 0,
            // });
        }
        
        const status = await store.dispatch('userData/UpdateUserInfo');
        
        if (msg !== undefined) setTimeout(()=>{
            Vue.prototype.$toast.dismiss(msg)
        },500);

        // if (toLoginPage && status === -1){
        //     // msg.close();
        //     // Vue.prototype.$toast.dismiss(msg);
        //     // 前往登录页面
        //     setTimeout(()=>{
        //         Vue.prototype.$toastx({
        //             type: 'warning',
        //             title: '您还未登录，正在跳转至登录页面',
        //             timeout: 2000
        //         })
        //     },500);
        //     router.replace({name: 'r-auth-login'});
        //     return status;
        // }

        if (status === 1 && !store.state.userData.notice.init){
            setTimeout(()=>{
                Vue.prototype.$toastx({type:'success',title: '欢迎您！尊敬的 '+ store.state.userData.user.info.nickname},{
                    timeout: 2000
                })
            },1000)
        }
        if (!store.state.userData.notice.init){
            store.dispatch('userData/noticeInit');
        }

        return status;
    }
    return 1;
}