import request from '../scripts/utils/request'

export default {
    namespaced: true,
    actions: {
        async request(context,options){
                this.dispatch('dashboard/loadingChange', true);

            try {
                const response = await request(options);
                this.dispatch('dashboard/loadingChange', false);

                return response;
            } catch (error) {  
                this.dispatch('dashboard/loadingChange', false);

                if (error.response) {  
                    return Promise.reject(error.response);
                } else {  
                    return Promise.reject(error);
                }  
            }  
        }
    }
}