import VueRouter from 'vue-router'

import auth from './auth'
import dashboard from './dashboard'
import account from './account'
import admin from './admin'
import apps from './apps'

import pullData from '@/scripts/common/PullData'
import Vue from 'vue'

const baseRoutes = [
    {
        name: 'r-root',
        path: '/',
        component: () => import ('@/pages/Root/Root.vue'),
        redirect: { name: 'r-home' },

        children: [
            {
                name: 'r-home',
                path: 'home',
                component: () => import ('@/pages/Root/Home.vue')
            },
            {
                name: 'r-help',
                path: 'help',
                component: () => import ('@/pages/Root/Help.vue')
            },
            {
                name: 'r-about',
                path: 'about',
                component: () => import ('@/pages/Root/About.vue')
            }
        ]
    },
    {
      path:'*',
      component:()=>import('../views/404.vue')
    }
]

const routes = baseRoutes.concat(auth, dashboard, account, admin, apps)

const router = new VueRouter({
    mode: 'history',
    routes,
})

router.beforeEach(async (to, from, next) => {  
    const loginStatus = await pullData()
    const noLogin = [
        'r-root',
        'r-home',
        'r-help',
        'r-about',
        'r-auth-login',
        'r-auth-register',
        'r-auth-verify',
        'r-auth-reset',
        'r-auth-reset-password',
        'r-auth-reset-password-success'
    ]
    const toDashboard = [
        'r-auth-login',
        'r-auth-register',
    ]

    if (!noLogin.includes(to.name) && loginStatus === -1) {
        Vue.prototype.$toastx({
            type: 'warning',
            title: '请先登录'
        })
        next({ name: 'r-auth-login', replace : true })
    }

    if (toDashboard.includes(to.name) && loginStatus === 1) {
        next({ name: 'r-dashboard', replace : true })
    }

    next()
});

export default router